/* Media sizes */
@font-face {
  font-family: League_Spartan;
  src: url('/fonts/LeagueSpartan-Bold.otf');
}
@font-face {
  font-family: Montserrat;
  src: url('/fonts/Montserrat-Regular.otf');
}
@font-face {
  font-family: Montserrat;
  src: url('/fonts/Montserrat-bold.otf');
  font-weight: bold;
}
@font-face {
  font-family: Montserrat;
  src: url('/fonts/Montserrat-Light.otf');
  font-weight: 300;
}
* {
  box-sizing: border-box;
}
body {
  background-color: #F7F9FC;
}
h1,
h2 {
  font-family: League_Spartan;
  text-align: center;
}
a,
a:hover,
a:focus,
a:visited {
  text-decoration: none;
}
.link--underline {
  color: #3185fc;
}
p {
  font-size: 1rem;
  line-height: 1.6;
  color: #999999;
  font-weight: 300;
}
body,
input,
button,
input,
h3 {
  font-family: Montserrat;
}
html,
body {
  min-height: 100vh;
  font-size: 15px;
  letter-spacing: 0.5px;
  overflow-x: hidden;
}
body {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: flex-start;
  position: fixed;
  overflow-y: scroll;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
}
.body {
  width: 100%;
  margin: 0 auto;
  padding: 9rem 0 0 0;
  max-width: 1210px;
}
input,
body {
  font-size: 0.8rem;
}
.text {
  font-size: 0.8rem;
}
@media only screen and (max-width: 1200px) {
  .body {
    padding-left: 2rem;
    padding-right: 2rem;
  }
}
@media only screen and (max-width: 900px) {
  .body {
    overflow-y: scroll;
    -webkit-overflow-scrolling: touch;
  }
}
.header {
  position: fixed;
  width: 100%;
  background: #ffffff;
  border-bottom: 1px solid #d6d6d6;
  z-index: 999;
  height: 5rem;
  display: flex;
  align-items: center;
  justify-content: flex-start;
}
.header .header__container {
  width: 100%;
  max-width: 1210px;
  margin: auto;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.header .header__img {
  width: 11rem;
}
.header .header__link {
  color: #3185fc;
}
@media only screen and (max-width: 1200px) {
  .header .header__container {
    margin-left: 2rem;
    margin-right: 2rem;
  }
}
.footer {
  margin: auto auto 0 auto;
}
.footer__container {
  max-width: 1210px;
  margin: 4rem auto 2rem auto;
  text-align: center;
}
.footer .text {
  font-size: 0.8rem;
}
.section {
  width: 100%;
  border-radius: 3px;
  border: 1px solid #d6d6d6;
  margin: 0 auto 3rem auto;
  overflow: hidden;
}
.section .section__name {
  width: 100%;
  background: #ffffff;
  padding: 0.3rem 1rem;
  color: #3185fc;
}
.section .section__name--alone {
  border-bottom: 1px solid #d6d6d6;
}
.section .section__heading {
  font-weight: 300;
  text-align: left;
  font-size: 1rem;
}
.section .section__table-container {
  overflow-x: auto;
}
.section--search {
  margin: 0 auto 2rem auto;
}
.search {
  display: flex;
}
.search .search__input,
.search .search__button {
  padding: 0.6rem 1rem;
}
.search .search__input {
  flex: 1;
  border: none;
  font-size: 0.8rem;
}
.search .search__button {
  border: none;
  border-left: 1px solid #d6d6d6;
  color: #ffffff;
  background: #E84855;
  cursor: pointer;
}
@media only screen and (max-width: 650px) {
  .search {
    display: flex;
    flex-wrap: wrap;
  }
  .search .search__input,
  .search .search__button {
    width: 100%;
    padding: 1rem;
  }
  .search .search__button {
    border: none;
    border-top: 1px solid #d6d6d6;
  }
}
.table {
  width: 100%;
  text-align: left;
  display: table;
  border-collapse: collapse;
  border-spacing: 0;
}
.table__header {
  background: #ffffff;
  border-bottom: 1px solid #d6d6d6;
}
.table__header .table__cell {
  color: #000000;
  font-size: 0.8rem;
  padding: 0.8rem 1rem 0.8rem 1rem;
}
.table__row {
  display: table-row;
}
.table__row:nth-child(2n) {
  background: #ffffff;
  border-bottom: 1px solid #d1e1fb;
  border-top: 1px solid #d1e1fb;
}
.table__cell {
  padding: 0.8rem 1rem;
  font-size: 0.8rem;
  color: #4f4f4f;
}
.table__cell--counter {
  padding-left: 1.5rem;
}
.table__cell--desc-hidden {
  display: none;
}
.table__link {
  color: #3185fc;
}
@media only screen and (min-width: 1250px) {
  .table__row:hover {
    background: #dce8fc;
  }
  .table__row:nth-child(2n):hover {
    background: #e8f0fd;
  }
}
@media only screen and (max-width: 650px) {
  .table {
    table-layout: fixed;
  }
  .table__row {
    max-width: 100%;
    overflow: hidden;
  }
  .table__cell {
    width: 100%;
  }
  .table__cell--resp-hidden {
    display: none;
  }
  .table__cell--double {
    width: 200%;
  }
  .table__cell--desc-hidden {
    display: table-cell;
  }
  .table__transaction {
    width: 100%;
    display: block;
    text-overflow: ellipsis;
    overflow: hidden;
    white-space: nowrap;
  }
}
.list {
  padding: 1rem;
  width: 100%;
  font-size: 0.8rem;
  background: #ffffff;
  overflow-x: auto;
}
.list__row {
  display: flex;
  padding: 0.4rem 0;
}
.list__item--20w {
  width: 20%;
}
.list__item--80w {
  width: 80%;
}
.list__href {
  color: #3185fc;
}
@media only screen and (max-width: 750px) {
  .list__item {
    word-break: break-all;
  }
  .list__item--20w {
    width: 40%;
  }
  .list__item--80w {
    width: 60%;
  }
}
@media only screen and (max-width: 450px) {
  .list__row {
    flex-direction: column;
  }
  .list__item {
    width: 100%;
  }
  .list__item--20w {
    margin-bottom: 0.3rem;
    color: #999999;
  }
  .list__item--80w {
    margin-bottom: 0.5rem;
  }
}
.pagination {
  margin: 0;
  padding: 0;
  background: #ffffff;
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
}
.pagination__item {
  list-style-type: none;
  margin: 2rem 0.5rem 1rem 0.5rem;
  font-size: 0.8rem;
}
.pagination__item:last-child {
  display: none;
}
.pagination__line {
  margin: 2rem 0.4rem 1rem 0.4rem;
}
.pagination__link {
  color: #3185fc;
}
@media only screen and (max-width: 1075px) {
  .pagination__item {
    margin-left: 0;
    margin-right: 0;
    width: 20%;
    text-align: center;
  }
  .pagination__line {
    display: none;
  }
}
@media only screen and (max-width: 480px) {
  .pagination__item {
    width: 33%;
  }
}
.pop-up {
  z-index: 9999;
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  width: 100%;
  pointer-events: none;
  height: 100%;
  background: rgba(0, 0, 0, 0.2);
  align-items: center;
  justify-content: center;
}
.pop-up .content-container {
  pointer-events: all;
  position: fixed;
  background: #ffffff;
  border-radius: 10px;
  padding: 4rem 0;
}
.pop-up .close-button-container {
  position: absolute;
  top: 1rem;
  right: 1rem;
  cursor: pointer;
}
.pop-up .close-button-container .img {
  width: 1rem;
  height: 1rem;
}
.pop-up .headings {
  padding: 0 8rem;
  margin-bottom: 4rem;
}
.pop-up .main-heading {
  text-align: center;
  color: #3185fc;
}
.pop-up .row-container {
  position: relative;
  max-height: 15.55rem;
  overflow-x: scroll;
}
.pop-up .row-container .row {
  padding: 1.5rem 0;
  margin: 0;
  width: 100%;
  cursor: pointer;
}
.pop-up .row-container .row:nth-child(2n+1) {
  background: #F8F8F8;
}
.pop-up .row-container .row:hover {
  background: #d1e1fb;
}
.pop-up .row-container .row:nth-child(2n+1):hover {
  background: #F7F9FC;
}
.pop-up .row-container .text {
  margin: 0 0 0 2rem;
  text-transform: inherit;
  font-size: 0.9rem;
  color: #000000;
}
.pop-up .row-container .row.active {
  background: #F7F9FC;
}
.pop-up .row-container .row.active .text {
  color: #3185fc;
}
.pop-up .form-container {
  padding: 0 5rem;
}
.pop-up .form-container input {
  width: 100%;
}
.pop-up .controls-container {
  margin-top: 4rem;
  display: flex;
  justify-content: center;
}
.pop-up .controls-container .button {
  padding-left: 6rem;
  padding-right: 6rem;
}
.pop-up.closed {
  display: none;
}
.pop-up.open {
  display: flex;
}
@media only screen and (max-height: 450px) and (orientation: landscape) {
  .pop-up .content-container {
    border-radius: 0;
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    padding: 0;
  }
  .pop-up .headings {
    margin: 2rem auto;
  }
  .pop-up .controls-container {
    margin: 2rem auto;
  }
}
@media only screen and (max-width: 650px) {
  .pop-up .content-container {
    border-radius: 0;
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
  }
  .pop-up .headings {
    margin: 0;
  }
  .pop-up .row-container {
    max-height: inherit;
  }
}
.pop {
  position: absolute;
  background: #ffffff;
  width: 30rem;
  right: 0;
  margin-right: 5rem;
  box-shadow: 9px 8px 69px -20px #a3a3a3;
  z-index: 999;
  border-radius: 10px;
  padding: 2rem;
}
.pop .pop__text {
  font-size: 0.8rem;
  color: #000000;
  text-transform: inherit;
}
.pop .pop__text--blue {
  color: #3185fc;
}
.pop::after {
  content: '';
  position: absolute;
  top: 50%;
  left: 100%;
  width: 20px;
  height: 20px;
  margin-left: -10px;
  transform: rotate(-45deg);
  margin-top: -10px;
  background: #ffffff;
}
.pop--hidden {
  display: none;
}
.pictogram {
  padding-left: 1.3rem;
}
.pictogram--search {
  background: url('/images/layout/search.svg') no-repeat;
  background-size: 0.8rem 0.8rem;
  background-position: center left;
}
.pictogram--block {
  background: url('/images/layout/block.svg') no-repeat;
  background-size: 0.9rem 0.9rem;
  background-position: center left;
}
.pictogram--age {
  background: url('/images/layout/age.svg') no-repeat;
  background-size: 0.9rem 0.9rem;
  background-position: center left;
}
.pictogram--transaction {
  background: url('/images/layout/transaction.svg') no-repeat;
  background-size: 0.9rem 0.9rem;
  background-position: center left;
}
.pictogram--fee {
  background: url('/images/layout/fee.svg') no-repeat;
  background-size: 0.9rem 0.9rem;
  background-position: center left;
}
.pictogram--in-out {
  background: url('/images/layout/in-out.svg') no-repeat;
  background-size: 0.9rem 0.9rem;
  background-position: center left;
}
.pictogram--mixin {
  background: url('/images/layout/mixin.svg') no-repeat;
  background-size: 0.9rem 0.9rem;
  background-position: center left;
}
.pictogram--save {
  background: url('/images/layout/save.svg') no-repeat;
  background-size: 0.9rem 0.9rem;
  background-position: center left;
}
.info .text {
  margin-bottom: 1rem;
  text-align: center;
}
